main {
    padding:36px;
}
.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 17px
}
.meme--top,.meme--bottom {
    border: 1px solid #D5D4D8;
    border-radius: 5px;
    font-family: 'Karla', sans-serif; 
    text-indent: 5px ;
}
.form-btn {
    color: white;
    grid-column: 1/-1;;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: 700;
    cursor: pointer;
    font-family: 'Karla', sans-serif;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
}
.meme-image {
    max-width: 550px;
    border-radius: 3px;
}
.meme {
    position: relative;
}
.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}